
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;500;600;700;800;900&display=swap');

/* STYLES */

* {
  font-family: 'Inter';
  font-size: 14pt;
  transition: .4s;
  overflow-x: hidden;
}

img {
  user-select: none;
}

a {
  text-decoration: none;
}

.title {
  font-family: 'Poppins', sans-serif;
  font-size: 35px;
  text-align: center;
  font-weight: bolder;
  margin-bottom: -10px;
  line-height: 45px;
  overflow:hidden;
}

#container {
  display: flex;
  flex-flow: column;
}

/* HERO SECTION */

#hero {
  width: 100%;
  position: relative;
  top:0;
  align-items: center;
  justify-content: center;
  opacity: 0;
  animation-name: fadein;
  animation-duration: .4s;
  animation-play-state: running;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

#hero .hero-bg {
  position: relative;
  top:0;
  right: 0;
  width: 100%;
  height: 200px;
  background-image: url('assets/hero_bg.svg');
  background-size: cover;
  z-index: 1;
}

.hero-title {
  position: relative;
  top: -56px;
  height: fit-content;
  z-index:2;
  display: flex;
  flex-direction:column;
  align-items: center;
}

.logo {
  height: 100px;
  width: 100px;
  border: 5px solid white;
  border-radius: 20px;
}

.hero-title-name {
  text-align: center;
  font-size: 35px;
  line-height: 50px;
}

.chip {
  height: auto;
  width: auto;
  color:white;
  padding: 10px;
  font-weight: bold;
  border-radius: 40px;
  margin-top:5px;
  font-size: small;
  background: linear-gradient(90deg,#857dfc,#0054FF);
}

.Para p {
  margin: 0;
}

.Para {
  background-image: url('assets/footer_bg.jpg');
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 40px;
  margin-top:-30px;
  border-radius: 20px;
  padding: 30px;
}

#hero p {
  position: relative;
  top: -56px;
  margin: 20px;
  text-align: justify;
  text-indent: 30px;
}


/* FEATURES SECTION*/

#features, #subscribe, #footer {
  position: relative;
  margin-top: 20px;
  display: flex;
  flex-flow: column;
  padding-bottom: 60px;
}

.features-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
  left: 0;
}

.features_table {
  position: relative;
  z-index: 2;
  align-self: center;
  margin-top: 20px;
  text-align: center;
  width: 80%;
}

.features-bg tr, td {
  padding:15px;
}

.features_table tr:nth-child(odd) {
  background-color: rgba(255, 255,255,.88);
}

.features_table tr:nth-child(even) {
  background-color: rgba(255, 255,255,.09);
  color:white;
}


/* SUBSCRIBE SECTION */

#subscribe {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container_sub {
  margin-top: 20px;
  align-self: center;
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
  padding: 20px;
  justify-content: center;
  align-items: center;
  
}

.container_sub input {
  border: none;
  background-color: #e3e3e3;
  padding: 20px;
  border-radius: 50px;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  transition: .4s;
}

.container_sub input:focus {
  border: 1px solid black;
}

.container_sub input:focus {
  background-color: none;
}

#subs {
  border: none;
  border-radius: 50px;
  font-size: 20px;
  padding: 20px;
  background: linear-gradient(90deg,#857dfc,#0054FF);
  color: white;
  width: 200px;
  align-self: center;
  cursor: pointer;
  opacity: .85;
}

#subs:hover {
  opacity: 1;
}

#subs:active {
  transform: scale(.99);
}

.subscribed {
  background:url(assets/feature_bg.jpg);
  background-blend-mode: difference;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  align-self: center;
  font-weight: bold;
  font-size: 30px;
  text-align: center;
  margin-bottom: 0;
}


/* FOOTER SECTION */

#footer {
  position: relative;
  margin-top: -30px;
  right: 0;
  width: 100%;
  height: auto;
  background-repeat:no-repeat;
  justify-content: center;
  align-items: center;
  background-position: left;
  background-size: cover;
  background-image: url('assets/footer_bg.jpg');
}

#footer .icon_set {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.icon {
  margin: 5px;
  height: 30px;
  width: 30px;
  align-self: center;
  border-style: none;
  margin: 10px;
  cursor: pointer;
  opacity: .7;
  transition: .2s;
}

.icon:hover {
  opacity: 1;
}

.icon:active {
  opacity: .9;
}

.footer_options {
  margin-top:40px;
  display: flex;
  align-self: center;
  align-items: center;
  justify-content: space-between;
  align-content: center;
  flex-flow: row;
  flex-wrap:wrap;
  width: 70%;
  height:min-content;
}

.footer_items {
  align-items: center;
  align-self: center;
  margin:10px;
  margin-top:0;
}

.item_title {
  color:white
}

.footer_items ul {
  margin-top:10px;
  padding: 0;
}

.footer_items li {
  color:rgba(255, 255, 255, 0.354);
  list-style: none;
  margin-left: 0;
  position: relative;
  left:0;
  margin-bottom: px;
  cursor: pointer;
}

.footer_items li:hover {
  color:rgba(255, 255, 255, .7);
}

.footer_items li:active {
  color:rgba(255, 255, 255, .6);
}

#join_us {
  background-color: white;
  border:none;
  padding: 10px;
  border-radius: 50px;
  width: 100px;
  align-self: center;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  margin-top: 20px;
  opacity: .8;
}

#join_us:hover {
  opacity: 1;
}

#join_us:active {
  transform: scale(.98);
}

@media only screen and (max-width:520px) {
  .footer_items {
    margin-left:0;
    margin-right:0;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
